import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmPipControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-pip-control', VmPipControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  

export interface PipControlProps extends JSX.VmPipControl, HTMLAttributes<HTMLVmPipControlElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmPipControlElement, PipControlProps>(
  'vm-pip-control',
  new Set(['enterIcon','exitIcon','icons','tooltipPosition','tooltipDirection','hideTooltip','keys','isPiPActive','i18n','playbackReady']),
);
  